'use strict';

// 导入
import loadHtml from "./modules/loadHtml";
import loadBodyHtml from "./modules/common/loadBodyHtml";
import animationEnd from "./modules/animationEnd";
import Pagination from "./modules/Pagination";
import {stringToJson, jsonToQueryString} from "./modules/parser";
import {
	isArray, isUndefined, isNumber, isJq, isEqual, isFunction, isJson, once
} from "./modules/common/util";
import {pagination, popup, tab, table, eventBus} from "./modules/func";
import miniPopup from "./modules/miniPopup";
import getPopupInstance from "./modules/getPopupInstance";
import vConfirm from "./modules/vConfirm";
import vAlert from "./modules/vAlert";
import panel from "./modules/Panel";
import textarea from "./modules/Textarea";
import {storage} from "./modules/common/storage";
import form from "./modules/form";
import validate from "./modules/validate";
import {tooltip} from "./modules/tooltip";
import {scrollbarSize} from "./modules/common/scrollbarSize";
import {showLoading, hideLoading} from "./modules/loading";
import onResize from "./modules/common/onResize";
import message from "./modules/message";


// model
const exportModules = {
	// tooltip
	tooltip,
	// 加载
	loadHtml,
	// loadBodyHtml
	loadBodyHtml,
	// 分页
	Pagination,
	// 本地存储
	store: storage,
	// 表单相关方法
	form,
	// 表单验证
	validate,
	// 转json
	stringToJson, jsonToQueryString,
	// scrollbarSize
	scrollbarSize,
	//
	vConfirm, vAlert,
	//
	panel,
	textarea,
	// onResize 尺寸监听
	onResize,
	// 导出公共方法
	isArray, isUndefined, isNumber, isJq, isEqual, isFunction, isJson, once,
	// 导出简化调用方法
	pagination, popup, miniPopup, tab, table, eventBus,
	// get popup instance
	getPopupInstance,
	// animationEnd
	animationEnd,
	// loading
	showLoading, hideLoading,
	// message
	message,
	// alias
	set alias(aliasVal) {
		window[aliasVal] = exportModules;
	}
};

export default exportModules;


